import i18next from 'i18next'

/**
 *  i18next 
 * @author  <729234283@qq.com>
 * @example
 * // 
 * const i18n = new I18N('zh-CN').init().addResources(null, require.context('./locales/', true, /\.json$/))
 * // 
 * i18n.addResources('articles', require.context('./locales/', true, /\.json$/))
 * // 
 * const t = i18n.getT('articles')
 * // 
 * t('articleList')
 */
export default class I18N {
  /**
   * 
   * @param {string} lng 
   */
  constructor (lng) {
    this.lng = lng || this.getBrowserLanguage()
    this.ns = 'translation'
  }

  /**
   * 
   * @return {I18N}
   */
  init () {
    i18next.init({
      debug: true,
      lng: this.lng,
      fallbackNS: 'translation',
      load: 'all'
    })

    return this
  }

  /**
   * 
   * @param {string} ns 
   * @param {Function} req 
   * @return {I18N}
   */
  addResources (ns, req) {
    ns = ns || this.ns

    req.keys().forEach((path) => {
      const resources = req(path)
      const paths = path.split('/')
      const lng = paths[1]

      i18next.addResourceBundle(lng, ns, resources, true, true)
    })

    return this
  }

  /**
   * 
   * @return {string}
   */
  getBrowserLanguage () {
    return navigator.language || navigator.browserLanguage
  }

  /**
   * 
   * @param {string} lng 
   * @return {I18N}
   */
  switchTo (lng) {
    i18next.changeLanguage(lng)
    this.lng = lng

    return this
  }

  /**
   * 
   * @param {string} ns 
   */
  getT (ns) {
    return i18next.getFixedT(this.lng, ns || this.ns)
  }
}
